<template>
  <div class="information">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-tabs v-model="table.query.noticeType" @tab-click="handleClick">
        <el-tab-pane label="招生资讯" name="RECRUIT"></el-tab-pane>
        <el-tab-pane label="宣传区" name="BANNER"></el-tab-pane>
      </el-tabs>
      <div class="btn-block">
        <div @click="goToPublich()" class="btn-main-deepen">发布公告</div>
      </div>
      <!--<div @click="$router.push('/recruit/information/publicity')" class="btn-main ml15">宣传区设置</div>-->
      <search-box
          v-show="table.query.noticeType === 'RECRUIT'"
          :searchData="searchData" :table="table"></search-box>

      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="55%">标题</th>
          <th width="15%">{{table.query.noticeType === 'RECRUIT' ? '发布时间': '更新时间'}}</th>
          <th v-if="table.query.noticeType === 'RECRUIT'" width="15%">校区</th>
          <th width="15%">操作</th>
        </tr></thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>{{item.title}}</td>
          <td>{{item.createdTime | substrTime(16)}}</td>
          <td v-if="table.query.noticeType === 'RECRUIT'">{{getCampuName(item.campusId)}}</td>
          <td>
            <span @click="editFun(item.id)" class="oper">编辑</span>
            <span @click="remove(item)"class="oper">删除</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "招生资讯", path: ""}],
        tabs: 'info',
        searchData: [
          {type: 'select', title: '校区', model: 'campusId', option: []},
          {type: 'input', placeholder: '标题'}
        ],
        table: {
          url: '/rnotice/queryNoticePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            campusId: null,
            noticeType: this.$route.query.noticeType || 'RECRUIT',
            keywords: ''
          }
        }
      }
    },
    methods: {
      // 获取所有的校区
      async getCampusList() {
        let res = await this.$ajax({
          url: '/campus/queryCampusList.do'
        })
        this.searchData[0].option = []
        res.forEach(e => {
          this.searchData[0].option.push({
            label: e.name,
            value: e.id
          })
        })
      },
      // 获取表格中校区的名字
      getCampuName(id) {
        let arr = this.searchData[0].option
        let name = ''
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].value === id) {
            name = arr[i].label
            break
          }
        }
        return name
      },
      // 前往发布
      goToPublich(){
        if(this.table.query.noticeType === 'RECRUIT'){
          this.$router.push('/recruit/information/detail/0')
        }else{
          this.$router.push('/recruit/information/publicity/0')
        }
      },
      // 前往编辑
      editFun(id){
        if(this.table.query.noticeType === 'RECRUIT'){
          this.$router.push(`/recruit/information/detail/${id}`)
        }else{
          this.$router.push(`/recruit/information/publicity/${id}`)
        }
      },
      // 删除
      async remove(item) {
        await this.$confirm(`您确认删除【${item.title}】吗？`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        let url = '/rnotice/deleteNoticeBanner.do'
        if(this.table.query.noticeType === 'RECRUIT'){
          url = '/rnotice/deleteNotice.do'
        }
        await this.$ajax({
          url,
          query:{
            noticeId: item.id
          }
        })
        this.$remind('删除成功')
        this.$search(this.table)
      },
      handleClick() {
        this.table.query.keywords = ''
        this.table.query.campusId = null
        this.$search(this.table)
      }
    },
    mounted() {
      this.getCampusList()
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .information
    .btns
      position: absolute
      right: 15px
      top: 6px
      z-index: 9
</style>
